.countries {
  padding-top: 50px;
  width: 100%;
  display: grid;
  grid-gap: 50px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 20%));
  grid-template-rows: repeat(auto, 300px);
  justify-content: center;

  p {
    font-size: 1.2rem;
    font-weight: 600;
    width: 100%;
    text-align: left;
  }

  a {
    text-decoration: none;
  }
}

@media (min-width: 950px) {
  .countries {
    justify-content: space-between;
  }
}
